.section-search {
  box-shadow: inset 7px 10px 15px 0 rgba(0, 0, 0, 0.06); }

.filtros-search i {
  color: var(--primary-color);
  display: inline-block; }

.productos-search {
  padding-left: var(--bs-gutter-x, 0.75rem); }

.productos-col {
  padding-left: 1.5rem;
  padding-right: 1.5rem; }

.filtros-search {
  box-shadow: 9px 1px 13px -8px rgba(0, 0, 0, 0.25); }

.filtro-mobile {
  display: none; }

.dropdown-divider {
  height: 0;
  margin: 1rem 0;
  overflow: hidden;
  border-top: 1px solid #e9ecef; }

.wrapper {
  color: var(--primary-color); }

.toggle {
  width: 100%;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--primary-color) !important;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 10px 20px 10px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5); }

.contents {
  background: rgba(255, 255, 255, 0.2);
  font-size: 1rem;
  height: 0;
  overflow: hidden;
  transition: all 1s ease;
  border-radius: 0 0 5px 5px; }

.contents p {
  padding: 15px 30px 15px 0; }

.fab {
  color: #fff; }

.irs--flat .irs-bar {
  background-color: var(--primary-color) !important; }

.irs--flat .irs-from, .irs--flat .irs-to, .irs--flat .irs-single {
  background-color: var(--primary-color) !important; }

.irs--flat .irs-handle > i:first-child {
  background-color: #fcef7f !important; }

.irs--flat .irs-from:before, .irs--flat .irs-to:before, .irs--flat .irs-single:before {
  border-top-color: #fcef7f !important; }

#checkBlanco + label:before,
#checkNegro + label:before,
#checkGris + label:before,
#checkBlanco1 + label:before,
#checkNegro1 + label:before,
#checkGris1 + label:before {
  content: "";
  width: 20px;
  height: 20px;
  float: left;
  margin: 1px 0.5em 0 0;
  border: 1px solid var(--primary-color);
  border-radius: 10px; }

#checkBlanco:checked + label:before,
#checkNegro:checked + label:before,
#checkGris:checked + label:before,
#checkBlanco1:checked + label:before,
#checkNegro1:checked + label:before,
#checkGris1:checked + label:before {
  border: 3px solid var(--primary-color); }

#checkBlanco,
#checkNegro,
#checkGris,
#checkBlanco1,
#checkNegro1,
#checkGris1 {
  display: none; }

#checkBlanco + label:before,
#checkBlanco1 + label:before {
  background: #fff; }

#checkNegro + label:before,
#checkNegro1 + label:before {
  background: #000; }

#checkGris + label:before,
#checkGris1 + label:before {
  background: gray; }

.accordion-item {
  border: none !important; }

.accordion-button:not(.collapsed) {
  background-color: transparent !important;
  color: var(--primary-color) !important; }

.select-search {
  display: flex;
  justify-content: flex-end; }

.select-rounded {
  margin: 0; }

@media (max-width: 991.98px) {
  .filtro-mobile {
    display: block; }
  .filtros-search {
    display: none; }
  .productos-search {
    padding-left: 1rem;
    padding-right: 0; }
  .section-search {
    box-shadow: none; }
  .select-search {
    display: flex;
    justify-content: start; } }

.accordion-button::after {
  background-image: url("../images/angle_down.svg"); }

.accordion-button:not(.collapsed)::after {
  transform: none !important;
  background-image: url("../images/angle_up.svg"); }

.text-articulo {
  padding-left: 5px; }
